import { newSpecPage } from '@stencil/core/testing';
import { h } from '@stencil/core';
import { PrestoAudio } from '../presto-audio';

describe('presto-audio', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoAudio],
      html: `<presto-audio></presto-audio>`,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('renders autoplay', async () => {
    const page = await newSpecPage({
      components: [PrestoAudio],
      template: () => <presto-audio autoplay></presto-audio>,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('renders src', async () => {
    const page = await newSpecPage({
      components: [PrestoAudio],
      template: () => <presto-audio autoplay={false} src={'http://192.168.31.191:3000/wp-content/uploads/2021/11/bensound-ukulele.mp3'}></presto-audio>,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('renders preload', async () => {
    const page = await newSpecPage({
      components: [PrestoAudio],
      template: () => <presto-audio preload="" src={'http://192.168.31.191:3000/wp-content/uploads/2021/11/bensound-ukulele.mp3'}></presto-audio>,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('renders poster', async () => {
    const page = await newSpecPage({
      components: [PrestoAudio],
      template: () => <presto-audio preload="" poster="" src={'http://192.168.31.191:3000/wp-content/uploads/2021/11/bensound-ukulele.mp3'}></presto-audio>,
    });
    expect(page.root).toMatchSnapshot();
  });

  it('renders track', async () => {
    const page = await newSpecPage({
      components: [PrestoAudio],
      template: () => <presto-audio tracks={[{ label: 'audioLable', src: 'http://bensound-ukulele.mp3', srcLang: 'en' }]}></presto-audio>,
    });
    expect(page.root).toMatchSnapshot();
  });
});
